/*************swing************/
@-webkit-keyframes swing {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }

  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}
@keyframes swing {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }

  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}


/*************swing hair************/
@-webkit-keyframes swinghair {
  0% {
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
  }

  100% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
}
@keyframes swinghair {
  0% {
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
  }

  100% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
}
