.App {
    background-color: rgba(0, 0, 0, 0.08);
}

.top-header {
    height: 40px;
    padding-left: 4%;
    padding-right: 4%;
    display: block;
    /* background-color: #f5f5f5; */
    line-height: 40px;
}

.top-header .inner {
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.button-group {
    float: right;
    margin-top: 5px;
}

.main-header {
    background-color: #ffffff00 !important;
    height: 60px;
    line-height: 60px;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
}

.App-logo {
    height: 60px;
    width: 60px;
    float: left;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.title {
    line-height: 64px !important;
    display: inline;
    color: #03A9F4 !important;
    font-family: 'Slidefu', monospace;
}

.search-input {
    width: 400px !important;
    line-height: 64px;
    margin-top: 17px;
    margin-left: 37px;
}

.main-menu {
    padding-left: 10% !important;
    padding-right: 10% !important;
    background-color: rgba(193, 205, 216, 0.175) !important;
}